/* Color Palette */

$shade: #424642;
$accent1: #d44000;
$accent2: #ffa500;
$background: #262625;
$font: #eeeeee;

//

/* Fonts */

@import url('https://fonts.googleapis.com/css2?family=Noto+Serif+JP:wght@500&display=swap');

//


/* Section specific imports */



//

body {
    margin: 0;
    background-color: #1E1E1E;
    display: flex;
    flex-direction: column;
    align-items: center;
    word-break: break-word;
    -webkit-word-break: break-word;
}

h1, h2, h3, h5, p {
    font-family: 'Noto Serif JP', serif;
    color: $font;
    font-size: 400 11px / 1.4;
}

.imax {
    display: flex;
    flex-flow: row wrap;
    justify-content: space-around;
    align-items: center;
    width: 80%;
    margin-top: 10vh;

    h3 {
        font-size: 40px;
        margin: 0;
    }
    
    p {
        font-size: 20px;
    }

    .image {
        position: relative;
        height: calc(20vw * 1.72);
        width: 25vw;
        min-height: 400px;
        min-width: 300px;
        background-image: url("../assets/wall.JPG");
        background-repeat: no-repeat;
        background-size: cover;
        border-radius: 4px;
        margin: 0vw 6vw 6vw 0vw;
        z-index: 99;

        &:before {
            content: "";
            left: 10px;
            top: 10px;
            position: absolute;
            width: 100%;
            height: 100%;
            border: solid 8px $accent2;
            border-radius: 4px;
            border-left: none;
            border-top: none;
            transition: all 0.5s ease;
        }

        &:hover::before {
            border: solid 8px $accent2;
            border-left: none;
            border-top: none;
            transform: translateX(10px) translateY(10px);
        }
    }

    .textbox {
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: flex-start;
        height: calc(20vw * 1.72);
        margin-top: 4vh;
        margin-bottom: 4vh;
    }
}

.page-wrapper {
    display: flex;
    flex-direction: column;
    align-items: center;
    
    &:before {
        content: "";
        right: 2vh;
        top: 2vh;
        position: absolute;
        width: 20vw;
        height: 20vh;
        border: solid 8px $accent1;
        border-radius: 4px;
        border-left: none;
        border-bottom: none;
    }

    h3.big {
        font-size: 40px;
        margin: 0;
    }

    #project-heading {
        align-self: flex-start;
        margin: 0 0 6vh 10vw;
    }

    .box-wrapper {
        display: flex;
        flex-flow: row wrap;
        align-items: flex-start;
        margin: 0 10%;

        .content-box {
            position: relative;
            width: 60vw;
            display: flex;
            flex-flow: column wrap;
            align-items: flex-start;
            margin: 40px;
            border-radius: 4px;
            border: solid 8px $shade;
            border-right: none;
            border-top: none;
            border-bottom: none;
            transition: all 0.5s ease;
            padding-right: 5%;

            &:hover {
                border: solid 8px $accent2;
                border-right: none;
                border-top: none;
                border-bottom: none;
            }

            .dropdown {
                display: none;
            }

            .project-textbox {
                z-index: 1;
                display: flex;
                flex-direction: column;
                margin-left: 20px;
                padding-right: 20px;

                .project-header {
                    display: flex;
                    flex-flow: row wrap;
                    align-items: center;

                    a {
                        text-decoration: none;

                        &:hover h2 {
                            color: $accent2;
                        }
                    }
                }

                .hashtag-wrapper {
                    display: flex;
                    flex-flow: row wrap;

                    .hash {
                        background-color: $accent1;
                        color: black;
                        font-weight: bolder;
                        margin-right: 10px;
                        padding: 2px 6px;
                        border-radius: 4px;

                        &.red {
                            background-color: red;
                        }

                        &.green {
                            background-color: green;
                        }

                        &.yellow {
                            background-color: yellow;
                        }
                    }
                }
            }

            .gitlink {
                background-image: url("../assets/github.svg");
                background-size: contain;
                background-repeat: no-repeat;
                height: 20px;
                width: 20px;
                margin-top: 4px;
                margin-right: 10px;

                &.none {
                    display: none;
                }
            }
        }
    }
}

.contact-wrapper {
    display: flex;
    flex-flow: column wrap;
    align-items: center;
    width: 100%;

    #contact-heading {
        align-self: flex-start;
        margin: 6vh 0 6vh 10vw;
    }

    .contact-textbox {
        display: flex;
        flex-flow: row wrap;
        justify-content: space-evenly;
        align-items: center;
        width: 80%;

        .svgcontainer {
            display: flex;
            height: 60px;
            width: 60px;
            background-repeat: no-repeat;
            background-size: contain;

            &.github {
                background-image: url("../assets/github_white.svg");
            }

            &.linkedin {
                background-image: url("../assets/linkedin_logo.png");
            }
        }
    }
}

.header {
    width: 100%;
    height: 6vh;
    display: flex;
    flex-direction: row;
    align-items: center;

    .brackets {
        margin: 0;
        margin-left: 10px;
        margin-right: 10px;
        color: $accent2;
        padding-top: 4px;

        &.one {
            margin-left: 4%;
        }
    }
}

.footer {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
    height: 5vh;
    margin-top: 4vh;
    background-color: $accent2;

    h5 {
        color: $background;
    }
}
